import {AVisualComponent} from "../component/abstract/a-visual-component";
import jquery = require("jquery");

export class Text extends AVisualComponent{
	constructor(p_text:string){
		super("label","");
		jquery(this.element).text(p_text).addClass("Text col-xs-12 col-sm-12 col-md-12");
	}
	public setText(p_text:string){
		jquery(this.element).text(p_text);
	}
}
